<template>
    <div>
        <div class="row">
            <p class="tit">登录</p>
        </div>
        <!-- <div class="row">
            <p></p>
        </div> -->
        <div class="row">
            <el-input v-model="phone" placeholder="手机号码"></el-input>
        </div>
        <div class="row">
            <el-input v-model="pwd" placeholder="密码"></el-input>
        </div>
        <div class="row">
            <el-button type="primary" class="sure">确定</el-button>
        </div>
        <div class="row">
            <router-link class="go-reg" tag="p" to="/register">注册</router-link>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                phone: '',
                pwd: ''
            }
        },
        methods() {

        },
        mounted() {
        }
    }

</script>

<style scoped>
    .row {
        margin: 15px 0;
    }

    .row .code-ipt {
        width: 50%;
        float: left;
    }

    .row .get-code {
        float: right;
    }

    .row .sure {
        width: 100%;
    }

    .row .go-reg {
        cursor: pointer;
        text-align: right;
        color: #475669;
    }
</style>


